.WordGrid {
  position: relative;
  width: var(--wg-draw-width);
  height: var(--wg-draw-height);
  border-radius: calc(var(--tile-diam) / 2);
  margin-top: calc(var(--tile-diam) + var(--tile-margin-vert));
  margin-bottom: var(--component-margin);
  margin-left: var(--component-margin);
  margin-right: var(--component-margin);
  transition: box-shadow 0.5s, width 0.5s;
}

.WordGrid.ChoosingBoardSpace {
  box-shadow: var(--suggestion-shadow);
}

.WGTile {
  background-color: var(--color-gray-main);
}

.WGTile.WithLetter {
  background-color: var(--color-blue-main);
  color: white;
}

.WGTile.Junction {
  border-width: var(--tile-shadow-width);
  border-style: solid;
  border-color: #00000050;
}
